body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.retail_wrapper {
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  display: flex;
}

.box {
  box-sizing: border-box;
  flex-direction: column;
  gap: 10px;
  width: calc(50% - 10px);
  display: flex;
}

@media screen and (min-width: 768px) {
  .retail_wrapper {
    padding: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .retail_wrapper {
    flex-direction: row;
  }
}

